.divider {
  width: 100vw;
  border-top: 2px solid #eee;
}

.hero-container {
  width: 100vw;
  display: flex;
  justify-content: center;
  position: relative;
  margin-top: 71px;
}

.hero {
  width: 80%;
}

.hero-container span {
  position: absolute;
  top: 40%;
  font-size: 140px;
  color: #fc5a03;
}

.text-bloc {
  width: 100vw;
  display: flex;
  justify-content: center;
  margin: 8.3333vw auto;
}

.text-bloc a {
  color: #0000ff;
}

.text-container {
  width: 50%;
  line-height: 2.5rem;
}

.text-container h3 {
  font-size: 1.8rem;
  font-weight: 500;
}

.text-container p {
  font-size: 1.1rem;
}

.image-bloc {
  width: 100vw;
  height: 850px;
  position: relative;
  overflow: hidden;
}

.media-bloc {
  width: 100vw;
  display: flex;
  justify-content: center;
  position: relative;
}

figure {
  width: 50%;
}

iframe {
  width: 100%;
}

.media-bloc img {
  width: 80%;
}

.media-bloc h3, .media-bloc a {
  font-size: 1.8em;
  font-weight: 500;
  line-height: 1.8em;
}

.media-bloc a {
  color: #0000ff;
}

.container {
  flex: 1;
}

footer {
  width: 80%;
  margin: 7em auto 0 auto;
  border-top: 2px solid #efefef;
  padding-top: 5em;
}

footer h1 {
  font-size: 3em;
}

.footer-main-item {
  font-size: 1.1em;
}

.footer-main-item ul {
  display: flex;
  flex-direction: column;
}

.footer-main-item form input:first-of-type {
  border: none;
  background-color: #eeeeee;
  width: 75%;
  padding: 0.5em;
  font-size: 1em;
  height: 2.2em;
}

.footer-main-item form input:last-of-type {
  border: none;
  background-color: #0000ff;
  width: 25%;
  color: #FFFFFF;
  padding: 0.5em;
  font-size: 1em;
  height: 2.2em;
}

.footer-main-item select {
  border: none;
  background-color: #eeeeee;
  padding: 0.7em;
  font-weight: 500;
}

.footer-main-item select:last-of-type {
  width: 50%;
  margin-left: 0.2em;
}

.footer-main-row {
  display: flex;
}

.footer-main-row .footer-main-item {
  flex: 1;
}


.footer-main-row .footer-main-item:nth-of-type(3) {
  flex: 1.3;
}

.footer-main-row:first-of-type {
  margin-bottom: 2em;
}

.footer-secondary {
  display: flex;
  justify-content: space-between;
  margin: 4em 0 6em 0;
}

.footer-secondary div {
  display: flex;
}

.footer-secondary div p {
  margin-right: 1em;
}

